<template>
  <div id="login">
    <el-card class="login-form-layout">
      <el-form
        autocomplete="on"
        :model="loginForm"
        ref="loginForm"
        label-position="left"
        :rules="rules"
        label-suffix=" : "
      >
        <h2 class="login-title color-main"></h2>
        <el-form-item prop="username">
          <el-input
            clearable
            name="username"
            type="text"
            v-model="loginForm.username"
            autocomplete="on"
            placeholder=""
          >
            <i slot="prefix" class="el-input__icon el-icon-user-solid"></i>
          </el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            clearable
            name="password"
            :type="pwdType"
            @keyup.enter.native="handleLogin"
            v-model="loginForm.password"
            autocomplete="on"
            placeholder=""
          >
            <i slot="prefix" class="el-input__icon el-icon-lock"></i>
            <i slot="suffix" class="el-icon-view" @click="showPwd"></i>
          </el-input>
        </el-form-item>
        <el-form-item label="" style="margin-bottom: 10px">
          <el-radio-group
            v-model="loginForm.userType"
            size="small"
            style="margin-left: 50px"
          >
            <el-radio-button label=""></el-radio-button>
            <el-radio-button label=""></el-radio-button>
            <el-radio-button label=""></el-radio-button>
          </el-radio-group>
        </el-form-item>
        <div class="register">
          <el-link class="register-link" type="primary" href="/register"
            ></el-link
          >
        </div>
        <el-form-item style="margin-bottom: 30px">
          <el-button
            style="width: 100%"
            type="primary"
            :loading="loading"
            @click.native.prevent="handleLogin"
            ></el-button
          >
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { checkLogin, getNickname } from "network/login/Login";
import { mapMutations } from "vuex";
export default {
  name: "Login",
  data() {
    return {
      userToken: "",
      loginForm: {
        username: "",
        password: "",
        userType: "",
      },
      loading: false,
      pwdType: "password",
      rules: {
        username: [
          { required: true, message: "!!", trigger: "blur" },
          {
            pattern: /^[_a-zA-Z0-9]+$/,
            message: "//!!",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "!!", trigger: "blur" },
          { min: 4, message: "4!!", trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    ...mapMutations(["changeLogin", "saveUsername","saveNickname"]),
    showPwd() {
      if (this.pwdType === "password") {
        this.pwdType = "";
      } else {
        this.pwdType = "password";
      }
    },
    saveLoginNick() {
      getNickname(this.loginForm.username).then(res => {
        this.saveNickname({nickname: res.data})
      }).catch(err => {
        console.log("");
      })
    },
    handleLogin() {
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          checkLogin(this.loginForm)
            .then((res) => {
              console.log(this.loginForm.userType);
              if (res.code == 200) {
                let msgs = res.msg.split("-");
                if (msgs[0] == "success") {
                  this.$message({
                    type: "success",
                    message: msgs[1],
                  });
                  // tokenvuexlocalStorage
                  this.userToken = res.data;
                  // console.log("token:" + this.userToken);
                  this.changeLogin({ Authorization: this.userToken });
                  // 
                  this.saveLoginNick();
                  this.saveUsername({ username: this.loginForm.username });
                  if (this.loginForm.userType === "") {
                    this.$router.push("/student");
                  } else if (this.loginForm.userType === "") {
                    this.$router.push("/teacher");
                  } else if (this.loginForm.userType === "") {
                    this.$router.push("/admin");
                  }
                  this.loginForm = { userType: "" };
                } else {
                  this.$message({
                    type: "warning",
                    message: msgs[1],
                  });
                  this.loginForm = { userType: "" };
                }
              } else {
                this.$message.error(res.msg);
                this.loginForm = { userType: "" };
              }
            })
            .catch((err) => {
              this.$message.error("404 ,");
            });
        } else {
          // eslint-disable-next-line no-console
          this.$message.error("");
          return false;
        }
      });
    },
  },
  beforeRouteEnter(to, from, next) {
    // 
    document
      .querySelector("body")
      .setAttribute(
        "style",
        "background:url('https://static.zhihu.com/heifetz/assets/sign_bg.db29b0fb.png') "
      );

    next();
  },

  beforeRouteLeave(to, from, next) {
    // 
    document.querySelector("body").setAttribute("style", "");

    next();
  },
};
</script>

<style scoped>
.login-form-layout {
  position: absolute;
  left: 0;
  right: 0;
  width: 400px;
  margin: 150px auto;
  border-top: 10px solid #409eff;
}

.login-title {
  text-align: center;
}

.login-center-layout {
  background: #409eff;
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
  margin-top: 200px;
}

.register {
  text-align: right;
  margin-bottom: 20px;
}
.register-link {
  margin-right: 10px;
  font-size: 16px;
}
</style>